import ServiceProvider from '../modules/core/ServiceProvider';

const svc = new ServiceProvider( {
    baseURL: upi.conf.svc.NEWSLETTER_API
} );

const NewsletterService = {
    createNewSubscriber( data ) {
        return svc.post( '/CreateNewSubscriber', {params: {...data, country: upi.env.COUNTRY_CODE}} )
            .then( ServiceProvider.responseData )
    }
};

export default NewsletterService;




