import '../css/homepage-en-us.scss';

import Modal from 'plugins/modal';
import View from './modules/core/View';
import NewsletterService from './services/NewsletterService';

// define submission type constants

var TYPE = {
    r: 'subscriptionInfo', //registrer
    u: 'subscriptionPrefs', //update preferences
    ulb: 'subscriptionPrefsLightbox' // update preferences lightbox
};

var emailValidator = null,
    form = null,
    registered_fields = {},
    message_wrap = null,
    submissionType = TYPE.r; // => { :subscriptionInfo, :subscriptionPrefs, subscriptionPrefsLightbox }

function init () {
    /* form validation and controller */
    emailValidator = $( '#aspnetForm' ).validate( {
        submitHandler: function ( form ) {
            handleEmailSubmission( form );
        }
    } );

    form = $( emailValidator.currentForm );

    // store collection of registered_field sets
    updateRegisteredFields( TYPE.r );
    // insert error message wrapper
    message_wrap = $( '<div id="emailmsg-container" />' ).prependTo( $( '#emailFormSignup' ) );

    // success
    form.on( 'email:reg-success', function () {
        Modal.getInstance().open( {
            width: '540px',
            content: '<section class="modal-body"><h1 style="font-size: 64px; color: #303030; font-weight: 100; letter-spacing: -1px;">Thank you!</h1></section>'
        } );

        message_wrap.empty();
        clearFields();
    } );

    // fail
    form.on( 'email:reg-error emailform:error', function ( e, data ) {
        var msg = '';
        switch ( parseInt( data.status ) ) {
            case 10:
                msg = 'Please enter a valid email address';
                break;
            case 11:
                msg = 'Email Address is a required field';
                break;
            case 12:
                msg = 'This email address has already been subscribed';
                break;
            case 13:
                msg = 'Please enter a valid 5 digit U.S. ZIP Code';
                break;
            case 14:
                msg = 'ZIP Code is a required field';
                break;
            default:
                msg = 'An error occured while processing your request. Please try again later.';
        }

        if ( msg !== '' ) {
            flashMessage( msg, 'error' );
        }
    } );

}

function handleEmailSubmission () {
    var form_data = parseForm( submissionType );
    submitRegistration( form_data );
}

function submitRegistration ( params ) {
    return NewsletterService.createNewSubscriber( params )
        .then( data => {
            if ( typeof data === 'string' ) {
                data = JSON.parse( data );
            }
            form.trigger( (parseInt( data.status ) === 1) ? 'email:reg-success' : 'email:reg-error', data );
        } )
        .catch( err => form.trigger( 'emailform:error', err ) );
}

function updateRegisteredFields ( id ) {
    registered_fields[id] = $( '#' + id ).find( 'input:not([type="submit"]), textarea, select' );
}

// eg. message.flash(), message.clear, new FlashMessage('#message-wrapper');
function flashMessage ( msg, type ) {
    type = type || '';
    if ( msg ) {
        var data = {
            type: type,
            message: msg
        };

        new View( 'messagebox' ).whenTemplatesLoaded.then( function () {
            message_wrap.html( this.render( data ) );
        } );
    }
}

function parseForm ( fieldset_id ) {
    var parsed_fields = [],
        results = {};

    // serialize registered_fields
    parsed_fields = registered_fields[fieldset_id].serializeArray();
    // merge to single object for JSON serialization
    $.each( parsed_fields, function ( i, item ) {
        results[item.name] = item.value;
    } );

    return results;
}

function clearFields () {
    for ( var key in registered_fields ) {
        if ( registered_fields.hasOwnProperty( key ) ) {
            registered_fields[key].each( function ( i, item ) {
                item.blur();
                item.value = '';
            } );
        }
    }
}

// initialize module
$( document ).ready( function () {
    $( '#' + TYPE.r ).injectFormLabels();
    $( '#aspnetForm' ).on( 'submit', function ( e ) {
        e.preventDefault();
    } );

    init();
} );

$( function () {
    // var $sales = $( '#hp-section-2' ),
    var $sales = $( '#body-content' ),
        defaults = ['navigation-style:progressive'],
        bp = upi.screen.getBreakPointValue(),
        promos = $sales.find( '.carousel' );

    initCarousel( $sales, defaults );

    $( window ).smartresize( function () {
        var curBreakPoint = upi.screen.getBreakPointValue();
        if ( curBreakPoint !== bp ) {
            initCarousel( $sales, defaults );
            bp = curBreakPoint;
        }
    } );

    function initCarousel ( scope, options ) {
        var opts = options.slice.apply( options );

        scope.corpus( 'off' );

        if ( Modernizr.mq( upi.mq.min.screenBase ) || !Modernizr.mq( 'only all' ) ) {
            opts.push( 'multiplier:4' );
        }
        else if ( Modernizr.mq( upi.mq.min.screenMedium ) ) {
            opts.push( 'multiplier:3' );
        }
        else if ( Modernizr.mq( upi.mq.min.screenSmall ) ) {
            opts.push( 'multiplier:2' );
        }
        else {
            opts.push( 'multiplier:1' );
        }
        // apply declarative settings

        promos.data( 'options', opts.join( ',' ).toString() );
        scope.corpus( 'carousel' );
    }
} );



